import "core-js/modules/es.number.constructor.js";
import { defineComponent } from 'vue';
import { ref } from 'vue';
import Editortable from '@/components/Editortable.vue';
import { useRoute, useRouter } from 'vue-router';
import { addProduct } from '@/api';
export default defineComponent({
  name: '',
  components: {
    Editortable: Editortable
  },
  setup: function setup(props, context) {
    var router = useRouter();
    var route = useRoute();
    var complete = ref(false);
    var paramsValue = ref({});
    var dialogVisible = ref(false);
    var message = ref('');
    var iconId = Number(route.params.productId) + 1; //console.log('443324444434444',iconId)

    var isactive = function isactive(context, params) {
      // console.log('55555555',context.value)
      console.log('6666666', params);
      paramsValue = params; //console.log('777777777',paramsValue)

      complete.value = context.value;
    };

    var resultcode = ref();

    var submitForm = function submitForm() {
      if (complete.value) {
        dialogVisible.value = true; //console.log('333333',dialogVisible.value)

        addProduct(paramsValue).then(function (response) {
          var res = response.data;
          resultcode.value = res.code;
          message.value = res.message; //console.log(4444444444444,message.value)
        }).catch(function (err) {
          return console.log(err);
        });
      }
    };

    var goHome = function goHome() {
      router.push({
        name: 'home'
      }); //console.log('666666666666',paramsValue)
    };

    var cancel = function cancel() {
      router.push({
        name: 'home'
      });
    };

    return {
      isactive: isactive,
      complete: complete,
      submitForm: submitForm,
      cancel: cancel,
      dialogVisible: dialogVisible,
      goHome: goHome,
      message: message,
      iconId: iconId // uploadImage,
      //     freshImage,
      //     headTip,
      //     imagesrc 

    };
  }
});