import "core-js/modules/es.json.stringify.js";
import { defineComponent } from 'vue';
import { ref } from 'vue';
import Editortable from '@/components/Editortable.vue';
import { useRoute, useRouter } from 'vue-router';
import { editorProduct } from '@/api';
import { compareObject } from '@/utils/compareObject';
export default defineComponent({
  name: '',
  components: {
    Editortable: Editortable
  },
  methods: {},
  setup: function setup(props, context) {
    var router = useRouter();
    var complete = ref(false);
    var paramsValue = ref({});
    var route = useRoute();
    var productInfo = JSON.parse(route.params.productInfo);
    console.log('222222222', productInfo.id);
    var copyproductInfo = JSON.parse(JSON.stringify(productInfo));
    var dialogVisible = ref(false);
    var message = ref('');
    var resultcode = ref();

    var isactive = function isactive(context, params) {
      // console.log('55555555',context.value)
      // console.log('6666666',params)
      paramsValue = params;
      complete.value = context.value;
      var copyParams = JSON.parse(JSON.stringify(paramsValue));
      copyParams.styleId = copyproductInfo.styleId;
      copyParams.id = copyproductInfo.id;

      if (productInfo.styleId !== JSON.parse(JSON.stringify(paramsValue)).styleId && compareObject(copyproductInfo, copyParams)) {
        complete.value = true;
      }
    };

    var submitForm = function submitForm() {
      if (complete.value) {
        //console.log('ooooo',JSON.parse(JSON.stringify(paramsValue)))
        var paramsVal = JSON.parse(JSON.stringify(paramsValue)); //console.log('1111ooooo',paramsValue)

        paramsVal["id"] = productInfo.id; //console.log('2222ooooo',paramsVal)

        editorProduct(paramsVal).then(function (response) {
          dialogVisible.value = true;
          var res = response.data;
          resultcode.value = res.code;
          message.value = res.message;
        }).catch(function (err) {
          return console.log(err);
        });
      }
    };

    var goHome = function goHome() {
      router.push({
        name: 'home'
      }); //console.log('666666666666',paramsValue)
    };

    var cancel = function cancel() {
      router.push({
        name: 'home'
      });
    };

    return {
      isactive: isactive,
      complete: complete,
      submitForm: submitForm,
      cancel: cancel,
      productInfo: productInfo,
      dialogVisible: dialogVisible,
      goHome: goHome,
      message: message
    };
  }
});