import "core-js/modules/es.number.constructor.js";
import "core-js/modules/es.array.splice.js";
import { defineComponent } from 'vue';
import { ref, toRaw } from 'vue';
import { addIcon } from '@/api';
export default defineComponent({
  name: '',
  props: {
    productInfo: Object,
    iconId: Number
  },
  setup: function setup(props, context) {
    console.log('3333hhhhhhhh', props.productInfo, props.iconId);
    var iconId = ref(0);

    if (props.iconId) {
      iconId.value = Number(props.iconId);
    }

    var productDescription = ref(''); //const textarea= ref<string>('')

    var productName = ref('');
    var scrollName = ref('');
    var productFunctions = ref([{
      functionName: '',
      functionContent: '',
      id: undefined,
      productId: undefined
    }]);
    var productAdvantages = ref([{
      advantageName: '',
      advantageContent: '',
      id: undefined,
      productId: undefined
    }]);
    var styleId = ref(0);
    var productId = ref(0); //

    var dialogVisible = ref(false);
    var message = ref('');

    if (props.productInfo) {
      productDescription.value = props.productInfo.productDescription;
      productName.value = props.productInfo.productName;
      scrollName.value = props.productInfo.scrollName;
      productFunctions.value = props.productInfo.productFunctions;
      productAdvantages.value = props.productInfo.productAdvantages;
      styleId.value = props.productInfo.styleId;
      productId.value = props.productInfo.id;
    }

    var iconid = ref(0);
    iconid.value = productId.value ? productId.value : iconId.value;
    console.log('55555555', iconid.value);

    var infoToParent = function infoToParent() {
      context.emit('isfinished', complete, {
        'productName': productName.value,
        'scrollName': scrollName.value,
        'productDescription': productDescription.value,
        'productFunctions': toRaw(productFunctions.value),
        'productAdvantages': toRaw(productAdvantages.value),
        'styleId': styleId.value
      });
    };

    var getRadioVal = function getRadioVal(e) {
      styleId.value = Number(e.target.value); //console.log(styleId.value)

      infoToParent();
    };

    var addfunction = function addfunction() {
      productFunctions.value.push({
        functionName: '',
        functionContent: '',
        id: undefined,
        productId: undefined
      }); //

      complete.value = false;
      infoToParent();
    };

    var addadvantage = function addadvantage() {
      productAdvantages.value.push({
        advantageName: '',
        advantageContent: '',
        id: undefined,
        productId: undefined
      });
      complete.value = false;
      infoToParent();
    };

    var deletefunction = function deletefunction(arr, val) {
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] == val) {
          arr.splice(i, 1);

          if (arr.length === 0) {
            complete.value = false;
          } else {
            complete.value = true;
          } //tableState()


          infoToParent();
          break;
        }
      }
    };

    var deleteadvantage = function deleteadvantage(arr, val) {
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] == val) {
          arr.splice(i, 1);

          if (arr.length === 0) {
            complete.value = false; //infoToParent()
          } else {
            complete.value = true; //infoToParent()
          } // tableState()


          infoToParent();
          break;
        }
      }
    }; //


    var countInput = ref(0);
    var countArea = ref(0);
    var complete = ref(false);

    var tableState = function tableState() {
      countInput.value = 0;
      countArea.value = 0;
      var form = document.getElementById("formId");

      if (form) {
        var tagEles = form.getElementsByTagName('input'); //console.log('33333333333',tagEles.length) //7

        for (var j = 0; j < tagEles.length; j++) {
          if (tagEles[j].value) {
            countInput.value += 1;
          }
        }

        var textareas = document.getElementsByTagName('textarea');

        for (var j = 0; j < textareas.length; j++) {
          if (textareas[j].value) {
            countArea.value += 1;
          }
        }

        complete.value = false; //
        //console.log(countInput.value,tagEles.length)

        if (countInput.value === tagEles.length && countArea.value === textareas.length && productFunctions.value.length && productAdvantages.value.length) {
          complete.value = true;
          infoToParent();
        }
      }
    };

    var KeyDown = function KeyDown() {
      tableState();
      infoToParent();
    }; //


    var headTip = ref("icon");
    var imagesrc = ref('');
    imagesrc.value = productId.value ? "productCard/addIcon?id=".concat(productId.value) : '';

    var uploadImage = function uploadImage() {
      var uploadEle = document.getElementById('upload');

      if (uploadEle) {
        uploadEle.click();
      }
    };

    var imageBlob = ref();

    var freshImage = function freshImage(e) {
      var imgObj = e.target.files[0]; //console.log('00000000000000',imgObj)
      // console.log(!!imgObj)
      // if(!!imgObj){
      //     var reader = new FileReader();
      //     reader.readAsArrayBuffer(imgObj)
      //     reader.onload = function(){
      //         var result=reader.result
      //      //
      //       imageBlob.value=new Blob([reader.result as BlobPart],{type:'mimeString'})
      //        console.log('111111111',result);
      //      //
      //      console.log('2221111',imageBlob.value)
      //     }
      // }

      var fr = new FileReader();
      fr.readAsDataURL(imgObj);

      fr.onload = function () {
        imagesrc.value = fr.result;
      };

      headTip.value = '';
    }; //icon


    var addicon = function addicon() {
      var form = document.forms.namedItem('fileinfo');

      if (form) {
        var fileData = new FormData(form); //console.log('33hhhhhhhhh',imagesrc.value)

        if (imagesrc.value) {
          // icon id
          addIcon(iconid.value, fileData).then(function (response) {
            var res = response.data;

            if (res.code === '200') {
              dialogVisible.value = true;
              message.value = res.message; //alert("")
            }
          }).catch(function (err) {
            return console.log(err);
          });
        } else {
          dialogVisible.value = true;
          message.value = 'icon';
        }
      }
    }; //console.log()


    return {
      productDescription: productDescription,
      productFunctions: productFunctions,
      addfunction: addfunction,
      productAdvantages: productAdvantages,
      addadvantage: addadvantage,
      deleteadvantage: deleteadvantage,
      deletefunction: deletefunction,
      KeyDown: KeyDown,
      complete: complete,
      tableState: tableState,
      productName: productName,
      scrollName: scrollName,
      styleId: styleId,
      productId: productId,
      getRadioVal: getRadioVal,
      infoToParent: infoToParent,
      uploadImage: uploadImage,
      freshImage: freshImage,
      headTip: headTip,
      imagesrc: imagesrc,
      imageBlob: imageBlob,
      addIcon: addIcon,
      addicon: addicon,
      iconid: iconid,
      message: message,
      dialogVisible: dialogVisible
    };
  }
});