import "core-js/modules/es.json.stringify.js";
import { defineComponent } from 'vue';
import { ref } from 'vue';
import { getInfo, deleteProduct } from '@/api';
import { useRouter } from 'vue-router';
export default defineComponent({
  name: 'ProductCard',
  components: {},
  setup: function setup() {
    var prolist = ref([]);
    var productLen = ref(0);
    var productId = ref(0);
    var dialogVisible = ref(false);
    var message = ref('');
    var resultcode = ref();
    var baseUrl = window.location.origin + '/service-core/rest';
    console.log(baseUrl);

    var getpro = function getpro() {
      getInfo().then(function (response) {
        var res = response.data;

        if (res.code === '200') {
          // console.log('1111111111',res.results)
          prolist.value = res.result;
          console.log('1111fsdfsgswefwe1', prolist.value);

          if (prolist.value) {
            productLen.value = prolist.value.length;
            productId.value = prolist.value[productLen.value - 1].id; //console.log('hhhhhhhh',productId.value)
          }
        }
      }).catch(function (err) {
        return console.log(err);
      });
    };

    getpro();
    var router = useRouter();

    var addPro = function addPro() {
      router.push({
        name: 'addProduct',
        params: {
          productId: productId.value
        }
      });
    };

    var editorPro = function editorPro(e) {
      //console.log(JSON.stringify(e))
      router.push({
        name: 'editorProduct',
        params: {
          productInfo: JSON.stringify(e)
        }
      });
    };

    var deletePro = function deletePro(e) {
      deleteProduct(e).then(function (response) {
        dialogVisible.value = true;
        var res = response.data;
        resultcode.value = res.code;
        message.value = res.message; // if(res.code==='200'){
        //   alert(res.message)
        //  // prolist.value=res.result
        //   getpro()
        // }
      }).catch(function (err) {
        return console.log(err);
      });
    };

    var goHome = function goHome() {
      dialogVisible.value = false; // console.log('22222')

      getpro(); //console.log('666666666666',paramsValue)
    };

    return {
      prolist: prolist,
      addPro: addPro,
      editorPro: editorPro,
      deletePro: deletePro,
      deleteProduct: deleteProduct,
      getpro: getpro,
      dialogVisible: dialogVisible,
      goHome: goHome,
      message: message,
      productId: productId,
      productLen: productLen,
      baseUrl: baseUrl
    };
  }
});